<template>
	<div id="map" style="height: 100%; background-color: purple;"></div>
</template>


<script>

	var geoCoordMap = {
    	"武汉":[114.31,30.52],
    	"大庆":[125.03,46.58],
    	"test":[50,50]
	};

	var convertData = function (data) {
    	var res = [];
    	for (var i = 0; i < data.length; i++) {
    	    var geoCoord = geoCoordMap[data[i].name];
    	    if (geoCoord) {
    	        res.push(geoCoord.concat(data[i].value));
    	    }
    	}
    	return res;
	};

  function onPoint()
  {
    console.log(this.position);

  }

  import echarts from 'echarts'
	export default
	{
		name:'',		
		methods:{
            drawPie(id){
               let myChart = echarts.init(document.getElementById(id))
               myChart.setOption({
    				title: {
    				    text: 'china',
    				    left: 'center',
    				    textStyle: {
    				        color: '#fff'
    				    }
    				},
    				tooltip: {
    				    trigger: 'item'
    				},
            legend: {
              orient: 'vertical',
              top: 'bottom',
              left: 'right',
              data:['test'],
              textStyle: {
                color: '#fff'
              }
            },
            geo: {
        				map: 'china',
        				roam: true,
                scaleLimit:{
                  max:10,min:1
                },
                center: [115,23],
                zoom:7,
        				label: {
        				    emphasis: {
        				        show: false
        				    }
        				},
        				itemStyle: {
        				    normal: {
        				        areaColor: '#323c48',
        				        borderColor: '#111'
        				    },
        				    emphasis: {
        				        areaColor: '#2a333d'
        				    }
        				}
    				},
            visualMap:{
              min:0,
              max:300,
              type: 'piecewise',
              splitNumber:2
            },
            series: [
   					    {
   					        name: 'test',
   					        type: 'scatter',
   					        coordinateSystem: 'geo',
   					        data: convertData([
   					            {name: "武汉", value: 273},
   					            {name: "大庆", value: 279},
   					            {name: "test", value: 29}
   					        ]),
   					        symbolSize: 12,
   					        label: {
   					            normal: {
   					                show: false
   					            },
   					            emphasis: {
   					                show: false
   					            }
   					        },
   					        itemStyle: {
   					            emphasis: {
   					                borderColor: '#fff',
   					                borderWidth: 1
   					            }
   					        }
   					    }
   					]
               });

             	myChart.on('click',function(param){
                  var pos = [param.event.offsetX,param.event.offsetY];
                  var geopos = myChart.convertFromPixel('geo', pos);
                  geoCoordMap["test"] = geopos;
                  console.log(geopos);

                  myChart.setOption({
                    series:{
                      data: convertData([
                        {name: "武汉", value: 273},
                        {name: "大庆", value: 279},
                        {name: "test", value: 29}
                    ]),
                    }
                  });
               	});
            }

		},

		mounted()
		{
            this.$nextTick(function() {
                this.drawPie('map')
            })
		}
	}
</script>

<style scoped>
    * {
        margin: 0;
        padding: 0;
        list-style: none;
    }
</style>



